import { constants } from "../constants.js";
import Datepicker from "vuejs-datepicker";
export default {
  components: {
    Multiselect,
    TermFacet,
    Lightbox,
    Spinner,
    Datepicker,
  },

  props: {
    type: {
      type: String,
    },
    state: {
      type: String,
    },
    priority: {
      type: String,
    },
    language: {
      type: String,
    },
    isRetouchedVersion: {
      type: Boolean,
    },
    rightsType: {
      type: String,
    },
    usageChannel: {
      type: String,
    },
