    },
    productBrand: {
      type: String,
    },
    productLine: {
      type: String,
    },
    productYear: {
      type: String,
    },
    productGeneration: {
      type: String,
    },
    productVersion: {
      type: String,
    },
    retailAssetTypes: {
      type: String,
    },
    productType: {
      type: String,
    },
    productSize: {
      type: String,
    },
    productColor: {
      type: String,
    },
    productLaunchedCountries: {
      type: String,
    },
    marketnames: {
      type: String,
    },
  },

  data() {
    return {
      file_content_mime_type_agg: undefined,
      dublincore_expired_agg: undefined,
      fulltext: undefined,
      dublincore_title: undefined,
      facetedKeyword_keywords_label: undefined,
      userInput: null,
      amazon_original_creator_name: undefined,
      dublincore_publisher: undefined,
      product_product_id: undefined,
      reviewMin: undefined,
      reviewMax: undefined,
      liveMin: undefined,
      liveMax: undefined,
      deliverMin: undefined,
      deliverMax: undefined,
      expireMin: undefined,
      expireMax: undefined,
      productionMin: undefined,
      productionMax: undefined,
      purchaseMin: undefined,
      purchaseMax: undefined,
      results: [],
      pagesize: { value: 24, label: "24" },
      searchSelect: [],
      isLoading: false,
      currentPage: 1,
      total: 0,
      sizes: [
        { value: 12, label: "12" },
        { value: 24, label: "24" },
        { value: 48, label: "48" },
        { value: 96, label: "96" },
      ],
      sortBy:{},
      sortByData: [
        { value: 'dc:title', label: "Title" },
        { value: 'dc:created', label: "Created" },
        // { value: 'dublincore_expired_min', label: "Expiration Date" }
      ],
      typeFilter: undefined,
      priorityFilter: undefined,
      stateFilter: undefined,
      languageFilter: undefined,
      isRetouchedFilter: undefined,
      rightsTypeFilter: undefined,
      retailAssetTypeFilter: undefined,
      retailAssetUseFilter: undefined,
      channelFilter: undefined,
      subChannelFilter: undefined,
      usageGeographyFilter: undefined,
      usageLanguageFilter: undefined,
      workspaceFilter: undefined,
      agenciesFilter: undefined,
      retailersFilter: undefined,
      vendorsFilter: undefined,
      roleFilter: undefined,
      formatFilter: undefined,
      colorSpaceFilter: undefined,
      productBrandFilter: undefined,
      productLineFilter: undefined,
      productYearFilter: undefined,
      productGenerationFilter: undefined,
      productVersionFilter: undefined,
      productCategoryFilter: undefined,
      productTypeFilter: undefined,
      productSizeFilter: undefined,
      productColorFilter: undefined,
      productLaunchedCountriesFilter: undefined,
      marketNamesFilter: undefined,
      facets: {},
      queryParams: {},
      queryRoute: {},
      selects: [],
      loading: false,
      searchFilters: {
        isSticky: false,
        isStickyBottom: false,
        offsetTop: 0,
        isVisibleOnMobile: false,
      },
      scrollTop: 0,
      listSortDefault: true,
      listSort: true,//1:a-z 0:z-a, null:default status
    };
  },

  computed: {
    images: function() {
      var images = [];
      this.results.forEach(function(item) {
        images.push({
          thumb: item.contextParameters.thumbnail.url,
          src: item.contextParameters.thumbnail.url,
          caption: item.title,
          document: item,
        });
      });
      return images;
    },
  },

  watch: {
    pagesize: function(val) {
      console.log("watch pagesize===", val);
      this.queryParams.pageSize = val.value;
      this.search();
    },
    currentPage: function() {
      this.queryParams.currentPageIndex = this.currentPage - 1;
      this.search();
    },
    $route() {
      if (this.$route.query.facetedKeyword_keywords_label != undefined) {
        this.facetedKeyword_keywords_label = this.$route.query.facetedKeyword_keywords_label;
      }
      this.setInitialFiltersFromRoute();
      this.search();
    },
    // fulltext: function(val) {
    //   console.log("watch fulltext===",val)
    //   this.fulltext = val.displayLabel;
    //   this.getSuggestion();
    // },
    // '$route.query.facetedKeyword_keywords_label': function() {
    //   if (this.$route.query.facetedKeyword_keywords_label != undefined) {
    //     this.facetedKeyword_keywords_label = this.$route.query.facetedKeyword_keywords_label;
    //   }
    //   this.search();
    // }
  },

  // TODO: Remove this so Mika doesn't get mad :troll:
  mounted() {
    this.setInitialFiltersFromRoute();
    this.queryParams.pageSize = this.pagesize.value;
    this.routeQuery = this.$route.query;
    Object.assign(this.queryParams, this.routeQuery);
    this.search();
    this.calculateSearchFiltersPosition();
  },

  methods: {
    changeSortByData(){
      if(!this.sortBy) return;
      this.listSort = true;
      this.queryParams.sortOrder = 'asc';
    },
    _handlelistSort(type){
      if(!this.sortBy) return;
      if(type){
        this.listSortDefault = false;
      }else{
        this.listSort = !this.listSort;
      }
      this.queryParams.sortBy = this.sortBy.value;
      this.listSort ? this.queryParams.sortOrder = 'asc' : this.queryParams.sortOrder = 'desc';
      
      this.search();
  
    },
    customFormatter(date) {
      return this.$moment(date).format("dd/MM/yyyy");
    },
    _fulltext() {
      console.log("_fulltext===", this.fulltext);
      this.queryParams.system_fulltext = this.fulltext;
      this.search();
    },

    _dublincoreTitle() {
      this.queryParams.dublincore_title = this.dublincore_title;
      this.search();
    },

    _onSearchKeywords() {
      this.search();
    },

    _creator() {
      this.queryParams.amazon_original_creator_name = this.amazon_original_creator_name;
      this.search();
    },

    _owner() {
      this.queryParams.dublincore_publisher = this.dublincore_publisher;
      this.search();
    },

    _productId() {
      this.queryParams.product_product_id = this.product_product_id;
      this.search();
    },

    _minReviewDate() {
      if (this.reviewMin) {
        this.queryParams.project_review_date_min = this.reviewMin.toJSON();
      } else {
        this.queryParams.project_review_date_min = undefined;
      }
      this.search();
    },

    _maxReviewDate() {
      if (this.reviewMax) {
        this.queryParams.project_review_date_max = this.reviewMax.toJSON();
      } else {
        this.queryParams.project_review_date_max = undefined;
      }
      this.search();
    },

    _minLiveDate() {
      if (this.liveMin) {
        this.queryParams.project_live_send_date_min = this.liveMin.toJSON();
      } else {
        this.queryParams.project_live_send_date_min = undefined;
      }
      this.search();
    },

    _maxLiveDate() {
      if (this.liveMax) {
        this.queryParams.project_live_send_date_max = this.liveMax.toJSON();
      } else {
        this.queryParams.project_live_send_date_max = undefined;
      }
      this.search();
    },

    _minProductionDate() {
      if (this.liveMin) {
        this.queryParams.product_production_date_min = this.productionMin.toJSON();
      } else {
        this.queryParams.product_production_date_min = undefined;
      }
      this.search();
