    setInitialFiltersFromRoute() {
      if (this.type) {
        this.queryParams.system_primaryType_agg = JSON.stringify([this.type]);
      }
      if (this.state) {
        this.queryParams.system_currentLifeCycleState_agg = JSON.stringify([
          this.state,
        ]);
      }
      if (this.priority) {
        this.queryParams.project_priority_agg = JSON.stringify([this.priority]);
      }

      if (this.retailAssetTypes) {
        this.queryParams.amazon_retailAssetTypes_agg = JSON.stringify([
          this.retailAssetTypes,
        ]);
      }

      if (this.retailAssetUse) {
        this.queryParams.amazon_retailAssetUse_agg = JSON.stringify([
          this.retailAssetUse,
        ]);
      }

      if (this.language) {
        this.queryParams.amazon_language_agg = JSON.stringify([this.language]);
      }

      if (this.isRetouchedVersion) {
        this.queryParams.amazon_is_retouched_version_agg = JSON.stringify([
          this.isRetouchedVersion,
        ]);
      }

      if (this.rightsType) {
        this.queryParams.usage_rights_type_agg = JSON.stringify([
          this.rightsType,
        ]);
      }

      if (this.usageChannel) {
        this.queryParams.usage_usagechannel_channel_agg = JSON.stringify([
          this.usageChannel,
        ]);
      }

      if (this.subChannel) {
        this.queryParams.usage_usagechannel_subchannel_agg = JSON.stringify([
          this.subChannel,
        ]);
      }

      if (this.usageGeography) {
        this.queryParams.usage_geography_agg = JSON.stringify([
          this.usageGeography,
        ]);
      }

      if (this.usageLanguage) {
        this.queryParams.usage_language_agg = JSON.stringify([
          this.usageLanguage,
        ]);
      }

      if (this.workspace) {
        this.queryParams.relations_workspaceLocation_agg = JSON.stringify([
          this.workspace,
        ]);
      }

      if (this.agencies) {
        this.queryParams.external_search_agencies_agg = JSON.stringify([
          this.agencies,
        ]);
      }

      if (this.retailers) {
        this.queryParams.external_search_retailers_agg = JSON.stringify([
          this.retailers,
        ]);
      }

      if (this.vendors) {
        this.queryParams.external_search_vendors_agg = JSON.stringify([
          this.vendors,
        ]);
      }

      if (this.role) {
        this.queryParams.amazon_creator_role_agg = JSON.stringify([this.role]);
      }

      if (this.format) {
        this.queryParams.picture_info_format_agg = JSON.stringify([
          this.format,
        ]);
      }

      if (this.colorSpace) {
        this.queryParams.picture_info_colorSpace_agg = JSON.stringify([
          this.colorSpace,
        ]);
      }

      if (this.productBrand) {
        this.queryParams.product_search_brand_agg = JSON.stringify([
          this.productBrand,
        ]);
      }

      if (this.productLine) {
        this.queryParams.product_search_pl_agg = JSON.stringify([
          this.productLine,
        ]);
      }

      if (this.productYear) {
        this.queryParams.product_search_year_agg = JSON.stringify([
          this.productYear,
        ]);
      }

      if (this.marketnames) {
        this.queryParams.es_market_names_agg = JSON.stringify([
          this.marketnames,
        ]);
      }

      if (this.productGeneration) {
        this.queryParams.product_search_generation_agg = JSON.stringify([
          this.productGeneration,
        ]);
      }

      if (this.productVersion) {
        this.queryParams.product_search_version_agg = JSON.stringify([
          this.productVersion,
        ]);
      }

      if (this.productCategory) {
        this.queryParams.product_search_category_agg = JSON.stringify([
          this.productCategory,
        ]);
      }

      if (this.productType) {
        this.queryParams.product_search_product_type_agg = JSON.stringify([
          this.productType,
        ]);
      }

      if (this.productSize) {
        this.queryParams.product_search_size_agg = JSON.stringify([
          this.productSize,
        ]);
      }

      if (this.productLaunchedCountries) {
        this.queryParams.product_search_launch_countries_agg = JSON.stringify([
          this.productLaunchedCountries,
        ]);
      }

      if (this.productColor) {
        this.queryParams.product_search_color_agg = JSON.stringify([
          this.productColor,
        ]);
      }

      if (this.facetedKeyword_keywords_label) {
        this.queryParams.facetedKeyword_keywords_label = this.facetedKeyword_keywords_label;
      }
    },

    addToCart(result) {
      this.$nuxeo
        .operation("javascript.as_add_to_cart")
        .input(result.uid)
        .params({
          cartId: this.$store.state.cartId,
        })
        .execute()
        .then(
          function() {
            this.$toasted.success(
              this.$t("message.search-added-to-cart"),
              constants.TOASTED_DEFAULT_OPTIONS
            );
            this.$store.dispatch("updateCart");
          }.bind(this)
        )
        .catch(function(error) {
          this.$toasted.error(
            this.$t("message.search-unable-to-add"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    removeFromCart(result) {
      this.$nuxeo
        .operation("javascript.as_remove_from_cart")
        .input(result.uid)
        .params({
          cartId: this.$store.state.cartId,
        })
        .execute()
        .then(
          function() {
            this.$toasted.success(
              this.$t("message.search-removed-from-cart"),
              constants.TOASTED_DEFAULT_OPTIONS
            );
            this.$store.dispatch("updateCart");
          }.bind(this)
        )
        .catch(function(error) {
          this.$toasted.error(
            this.$t("message.search-unable-to-remove"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    select(result) {
      if (this.selects.indexOf(result) < 0) {
        this.selects.push(result);
      }
    },

    unselect(result) {
      var index = this.selects.indexOf(result);
      if (index >= 0) {
        this.selects.splice(index, 1);
      }
    },

    isSelect(result) {
      return this.selects.indexOf(result) >= 0;
    },

    addSelectionToCart() {
      this.$nuxeo
        .operation("Collection.AddToCollection")
        .input(this.selects)
        .param(
          "collection",
          "/default-domain/UserWorkspaces/Guest/Shopping_Cart"
        )
        .execute()
        .then(
          function() {
            this.$store.dispatch("updateCart");
            this.clearSelects();
            this.$refs.selectionModal.hide();
            this.$toasted.success(
              this.$t("message.Selection Added to Cart"),
              constants.TOASTED_DEFAULT_OPTIONS
            );
          }.bind(this)
        )
        .catch(function(error) {
          this.$toasted.error(
            this.$t("message.Could Not Add Selection to Cart"),
            constants.TOASTED_DEFAULT_OPTIONS
          );
          throw error;
        });
    },

    clearSelects() {
      this.selects.splice(0, this.selects.length);
    },

    isInCart(result) {
      var isInCart = this.$store.getters.isInCart(result.uid);
      return isInCart;
    },

    hasFacet(facetName) {
      return (
        this.facets[facetName] &&
        this.facets[facetName].buckets &&
        this.facets[facetName].buckets.length > 0
      );
    },

    //UI stuff

    calculateSearchFiltersPosition() {
      var $searchFilters = this.$el.querySelector(".search-filters");
      this.searchFilters.offsetTop = $searchFilters.getBoundingClientRect().top;
    },

    handleScroll() {
      this.scrollTop =
        window.pageYOffset !== undefined
          ? window.pageYOffset
          : (
              document.documentElement ||
              document.body.parentNode ||
              document.body
            ).scrollTop;

      if (this.scrollTop > this.searchFilters.offsetTop) {
        var appFooterOffsetTop = document
          .querySelector(".app-footer")
          .getBoundingClientRect().top;
        var searchFiltersHeight = this.$el.querySelector(".search-filters")
          .clientHeight;

        if (appFooterOffsetTop < searchFiltersHeight) {
          this.searchFilters.isStickyBottom = true;
          this.searchFilters.isSticky = false;
        } else {
          this.searchFilters.isSticky = true;
          this.searchFilters.isStickyBottom = false;
        }
      } else {
        this.searchFilters.isSticky = false;
        this.searchFilters.isStickyBottom = false;
      }
    },

    toggleFiltersOnMobile() {
      this.searchFilters.isVisibleOnMobile = !this.searchFilters
        .isVisibleOnMobile;
    },
    selectSuggestion(e) {
      console.log("selectSuggestion=", e);

      this._fulltext();
    },
    getSuggestion(e) {
      console.log("e--------", e, JSON.stringify(e));
      if (!e || e == "") return;
      if (e.id) {
        this.facetedKeyword_keywords_label = e.displayLabel;
        this.setInitialFiltersFromRoute();
        this.search();
      } else {
        this.facetedKeyword_keywords_label = e;
        this.isLoading = true;

        this.setInitialFiltersFromRoute();
        this.search();
        this.$nuxeo
          .operation("Keyword.Suggestion")
          .params({
            searchTerm: this.facetedKeyword_keywords_label,
          })
          .execute()
          .then(
            function(data) {
              this.isLoading = false;
              this.searchSelect = data;
            }.bind(this)
          )
          .catch(function(error) {
            throw error;
          });
      }
    },
  },

  created() {
    if (this.$route.query.brandId) {
      this.queryParams.product_search_brand_agg = JSON.stringify([
        this.$route.query.brandId,
      ]);
    }

    window.addEventListener("scroll", this.handleScroll);
  },

  destroyed() {
    window.removeEventListener("scroll", this.handleScroll);
  },
};
</script>

